<template>
	<view :style="showMsk ? 'height: 100vh;overflow:hidden;' : ''">
		<uni-nav-bar style="margin-bottom: 20rpx;"  @clickLeft="back" left-icon="back" title="全部解析">
			<view slot="right">
				<text @click="openpicker" class="iconfont icon-caidan1"></text>
				<text>{{parseInt(currentIndex) + 1}}/{{ti_num}}</text>
			</view>
		</uni-nav-bar>
		
		
		<view  v-if="tiList[currentIndex] != undefined" class="shuati-wrao" @touchmove="handletouchmove" @touchstart="handletouchstart" @touchend="handletouchend">
			<!-- 题标题及描述部分 -->
			<view class="ti-top-wrap">
				<view class="title">
					<text class="ti-type">{{ti_type[tiList[currentIndex].type]}}</text>
					<text>{{tiList[currentIndex].title}}</text>
					<text class="score">({{Math.ceil(tiList[currentIndex].score)}}分)</text>
				</view>
				<image style="width:200rpx; height:200rpx;" v-if="tiList[currentIndex].answer.t_img != ''" :src="tiList[currentIndex].answer.t_img" mode=""></image>
				<imt-audio class="imtaudio" v-if="tiList[currentIndex].answer.t_audio != ''" :src="tiList[currentIndex].answer.t_audio"></imt-audio>
				<video class="ti-video" v-if="tiList[currentIndex].answer.t_video != ''" :src="tiList[currentIndex].answer.t_video" :poster="tiList[currentIndex].answer.t_video_img" controls></video>
			</view>
			
			<!-- 选项区 类型 0判断1单选2定项多选3简答4填空5不定项多选 -->
			<!-- 如果是选择题则序号 A B C D 如果填空题则 1 2 3-->
			<view class="ti-info-wrap">
						
					<!-- 判断题 rs为0错误 1正确 -->
					<block v-if="tiList[currentIndex].type == 0">
						<view class="ti-info-item tiinfo-item-panduan-right">
							<block v-if="tiList[currentIndex].answer.rs == '1'">
								<image class="duicuo-img" :src="duihao" mode="aspectFill"></image>
								<text class="green">正确</text>
							</block>
							<block v-else-if="tiList[currentIndex].rs_user.rs == '1'">
								<image class="duicuo-img" :src="cuohao" mode="aspectFill"></image>
								<text class="red">正确</text>
							</block>
							<block v-else>
								<text class="ti-info-item-xuhao">A</text>
								<text class="">正确</text>
							</block>
							
						</view>
						<view class="ti-info-item tiinfo-item-panduan-wrong">
							<block v-if="tiList[currentIndex].answer.rs == '0'">
								<image class="duicuo-img" :src="duihao" mode="aspectFill"></image>
								<text class="green">错误</text>
							</block>
							<block v-else-if="tiList[currentIndex].rs_user.rs == '0'">
								<image class="duicuo-img" :src="cuohao" mode="aspectFill"></image>
								<text class="red">错误</text>
							</block>
							<block v-else>
								<text class="ti-info-item-xuhao">B</text>
								<text class="">错误</text>
							</block>
							
						</view>
						
					</block>
					
					<!-- 单选题 -->
					<block v-else-if="tiList[currentIndex].type == 1">
						<view v-for="(item, index) in tiList[currentIndex].answer.ans" :key="index" class="ti-info-item">
							
							<block v-if="tiList[currentIndex].answer.rs == index">
								<image class="duicuo-img" :src="duihao" mode="aspectFill"></image>
								<text class="green">{{item}}</text>
							</block>
							<block v-else-if="tiList[currentIndex].rs_user.rs == index">
								<image class="duicuo-img" :src="cuohao" mode="aspectFill"></image>
								<text class="red">{{item}}</text>
							</block>
							<block v-else>
								<text class="ti-info-item-xuhao">{{ti_info_item_abc[index]}}</text>
								<text class="">{{item}}</text>
							</block>
							
						</view>
					</block>
					
					<!-- 定项选择题 -->
					<block v-else-if="tiList[currentIndex].type == 2">
						<view v-for="(item, index) in tiList[currentIndex].answer.ans" :key="index" class="ti-info-item">
							
							<block v-if="tiList[currentIndex].answer.rs.indexOf(index) != '-1'">
								<image class="duicuo-img" :src="duihao" mode="aspectFill"></image>
								<text class="green">{{item}}</text>
							</block>
							<block v-else-if="tiList[currentIndex].rs_user.rs.indexOf(index) != '-1'">
								<image class="duicuo-img" :src="cuohao" mode="aspectFill"></image>
								<text class="red">{{item}}</text>
							</block>
							<block v-else>
								<text class="ti-info-item-xuhao">{{ti_info_item_abc[index]}}</text>
								<text class="">{{item}}</text>
							</block>
						</view>
						
					</block>
					
					<!-- 简答题 -->
					<block v-else-if="tiList[currentIndex].type == 3">
						<textarea disabled maxlength="-1" class="txt-area" :value="tiList[currentIndex].rs_user.rs" placeholder-style="color:#C8C8C8;" />
					</block>
					
					<!-- 填空题 -->
					<block v-else-if="tiList[currentIndex].type == 4">	
						<!-- 根据答案 控制input数量 -->
						<view v-for="(item,index) in tiList[currentIndex].answer.ans" :key="index" class="ti-info-item ti-info-item-tiankong">
							<block v-if="tiankong.ans[currentIndex][index] == item[0]">
								<image class="duicuo-img" :src="duihao" mode="aspectFill"></image>
								<input disabled class="tiankong-input green" style="border-bottom: 2rpx solid #DDDDDD;" type="text" :value="tiankong.ans[currentIndex][index]" />
							</block>
							<block v-else-if="tiankong.ans[currentIndex][index] != undefined && tiankong.ans[currentIndex][index] != item[0]">
								<image class="duicuo-img" :src="cuohao" mode="aspectFill"></image>
								<input disabled class="tiankong-input red" style="border-bottom: 2rpx solid #DDDDDD;" type="text" :value="tiankong.ans[currentIndex][index]" />
							</block>
							<block v-else>
								<image class="duicuo-img" src="" mode="aspectFill"></image>
								<input disabled class="tiankong-input red" style="border-bottom: 2rpx solid #DDDDDD;" type="text" />
							</block>
						</view>
						
					</block>
					
					<!-- 不定项多选 -->
					<block v-else-if="tiList[currentIndex].type == 5">
						<view v-for="(item, index) in tiList[currentIndex].answer.ans" :key="index" class="ti-info-item">
							<block v-if="tiList[currentIndex].answer.rs.indexOf(index) != '-1'">
								<image class="duicuo-img" :src="duihao" mode="aspectFill"></image>
								<text class="green">{{item}}</text>
							</block>
							<block v-else-if="tiList[currentIndex].rs_user.rs.indexOf(index) != '-1'">
								<image class="duicuo-img" :src="cuohao" mode="aspectFill"></image>
								<text class="red">{{item}}</text>
							</block>
							<block v-else>
								<text class="ti-info-item-xuhao">{{ti_info_item_abc[index]}}</text>
								<text class="">{{item}}</text>
							</block>
						</view>
					</block>
					
					<!-- 答案和解析 -->
					<view class="ans-jiexi">
						<text style="font-weight: bold; margin-right: 10rpx;">答案:</text>
						<view class="ans">
							<text v-if="tiList[currentIndex].type == '0'">{{tiList[currentIndex].answer.rs == '1'  ? 'A' : 'B'}}</text>
							<text v-if="tiList[currentIndex].type == '1'">{{ti_info_item_abc[tiList[currentIndex].answer.rs]}}</text>
							<text v-if="tiList[currentIndex].type == '2'">{{dingxiang.right_ans[currentIndex]}}</text>
							<text v-if="tiList[currentIndex].type == '3'">{{tiList[currentIndex].answer.rs}}</text>
							<view v-if="tiList[currentIndex].type == '4'" v-for="(item, index) in tiankong.right_ans[currentIndex]">
								<text style="">{{item[0]}}</text>
							</view>
							
							<text v-if="tiList[currentIndex].type == '5'">{{budingxiang.right_ans[currentIndex]}}</text>
							<image v-if="tiList[currentIndex].answer.img != ''" src="tiList[currentIndex].answer.img" mode="aspectFill"></image>
						</view>
						<view class="jiexi">
							<view class="jiexi-title">解析</view>
							<view class="jiexi-content">
								{{tiList[currentIndex].parsing == '' ? '暂无解析' : tiList[currentIndex].parsing}}
							</view>
						</view>
						
						<view v-if="tiList[currentIndex].rs_user.isreview != '0' && tiList[currentIndex].rs_user.isreview != undefined" class="dianping">
							<view class="jiexi-title">点评</view>
							<view class="jiexi-content">
								<text>{{tiList[currentIndex].rs_user.review}}</text>
								<imt-audio class="imtaudio" v-if="tiList[currentIndex].rs_user.re_audio != ''" :src="tiList[currentIndex].rs_user.re_audio"></imt-audio>
							</view>
						</view>
						
						
					</view>
						
			</view>
		</view>
		
		<!-- 下方收藏 得分 存入错题本 -->
		<view class="bottom-nav">
			<view @click="storeQuestion" class="bottom-nav-left bottom-nav-item">
				<block v-if="storeData[currentQid] === true">
					<text class="iconfont icon-pingfenxingxing xingxing_checked"></text>
					<text class="score-txt">已收藏</text>
				</block>
				
				<block v-else>
					<text class="iconfont icon-pingfenxingxing"></text>
					<text class="score-txt">收藏</text>
				</block>
			</view>
			
			<view class="bottom-nav-middle bottom-nav-item">
				<text>此题得分:</text>
				<text class="score_fen" v-if="tiList[currentIndex] != undefined">{{Math.ceil(tiList[currentIndex].score2)}}</text>
			</view>
			
			<view @click="addCuotiben" class="bottom-nav-right bottom-nav-item">
				
				<text v-if="cuotibenData[currentIndex] == '0'">存入错题本</text>
				<text v-else>已存入错题本</text>
			</view>
			
		</view>
		
			
		<!-- 遮罩区域 -->
		<!-- 遮罩上层 -->
		<view @click="closezhetop" v-if="is_zhe_top == '1'" class="zhetop">
		</view>
		
		 <view v-if="is_zhe_show == '1'" class="zhe">
			<view class="zhe-title">题目列表</view>
			<!-- 题号区域 -->
			<view class="tihao-wrap">
				<block v-for="(item,index) in ti_num" v-if="tiList[index].rs_user.isok == '1'">
					<text @click="checkByTihao(index)" class="tihao-item tihao_right">{{parseInt(index)+1}}</text>
				</block>
				<block v-else>
					<text @click="checkByTihao(index)" class="tihao-item tihao_wrong">{{parseInt(index)+1}}</text>
				</block>
			</view>
		 </view>  
		
	</view>
</template>

<script>
	import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue';
	const app = getApp();
	
	export default {
		components:{
			uniNavBar
		},
		data() {
			return {
				mask: false,
				isShowscore: '', //是否显示分值
				exp: '', //时长（秒） 0表示已结束 -1无时间限制
				ti_num: 0,
				tiList: [], //题目列表
				currentIndex: '0', //当前页面索引
				currentQid: '1', //当前题号 (索引+1)
				// 0判断1单选2定项多选3简答4填空5不定项多选
				ti_type: ['判断', '单选', '定项', '简答', '填空', '不定项'],
				user_answer: {
					'dui_num' : 0,
					'cuo_num' : 0,
					'ti_data' : []
				},	
				ti_info_item_abc: ['A', 'B', 'C', 'D', 'E'], 
				testsid: '', //试题id
				tiend: [], //存储当前题的题目答题情况 {题号1: 选项X, 题号2: 选项X}
				// 定项选择题相关数据
				dingxiang: {
					'ans' : [], //存放每次点击答案时的选项id
					'abc':[], 
					'is_start_check': '', //是否已经开始选择
					'sub': [], //是否提交 题号:是否提交 {'1' : '1', '2': '1', ...}
					'right_ans' : [], //正确答案数组字符串[id1: 'A B C', id2: 'A B C D'] 
					'key' : {'0':'0', '1':'1', '2':'2', '3':'3'},
				},
				// 填空题相关数据
				tiankong: {
					'ans': [], //存放用户input输入的答案
					'sub' : [], //是否提交 id: 是否提交 {'1' : '1', '2': '1', ...}
					'is_start_check': '', //是否已经开始输入
					'right_ans' : []
				},
				// 不定项选择题相关数据
				budingxiang: {
					'ans': [], //存放用户每次点击答案时的选项id
					'abc': [], //{'is_checked': '0'}, 是否已经选择
					'is_start_check': '', //是否已经开始选择 
					'sub': [], //是否提交 题号:是否提交 {'1' : '1', '2': '1', ...}
					'right_ans' : [], //正确答案数组字符串[id1: 'A B C', id2: 'A B C D'] 
					'key' : {'0':'0', '1':'1', '2':'2', '3':'3', '4':'4'}, //占位key, 强制组件重新渲染
				},
				// 简答题相关数据
				jianda: {
					'ans': '', //用户填写的简答题答案
					'is_start_check': '', //是否已经开始输入
				},
				is_zhe_show: '0', //是否显示遮罩层
				is_zhe_top: '0', //是否显示遮罩层上半边
				strWindow: false,
				userinfo: '',
				duihao: '../../static/images/tongji_duihao.png',
				cuohao: '../../static/images/tongji_cuohao.png',
				storeData: [], //收藏题目相关数据 题号 => (true已收藏 false未收藏)
				cuotibenData: [], //错题本相关数据 题号 => (true已存入错题本 false未存入错题本)
				showMsk: false, //弹窗flag
			}
		},
		onLoad(option) {
			
			this.testsid = option.testsid;
			let type = option.type;
			this.currentQid = parseInt(this.currentIndex) + 1;
			let userInfo = uni.getStorageSync('userinfo');
			if(!userInfo) {
				uni.navigateTo({
					url: '../login/login',
				});
			}
			this.userinfo = userInfo;
			
			this.getTopic(this.testsid, type); // 3看全部
			
		},
		methods: {
			// 添加进错题本
			addCuotiben() {
				let gData = app.globalData; 
				let that = this;
				let status = '1';
				if(that.tiList[that.currentIndex].iswrong == '1') {
					// 已存入错题本
					status = '2';
				}
				
				uni.request({
					url: gData.site_url + 'Wrongbook.SetWrong',
					method: 'GET',
					data: {
						// uid token type 2考试 testsid qid status 1添加 2删除
						'uid' : that.userinfo.id,
						'token' : that.userinfo.token,
						'type' : 2,
						'testsid' : that.testsid,
						'qid' : that.currentQid,
						'status' : status
					},
					success: res => {
						
						if(res.data.data.code == '0') {
							
							if(that.tiList[that.currentIndex].iswrong == '1') {
								that.tiList[that.currentIndex].iswrong = '0';
							} else {
								that.tiList[that.currentIndex].iswrong = '1';
							}
							if(status == '1') {
								that.$set(that.cuotibenData, that.currentIndex, '1');
							} else {
								that.$set(that.cuotibenData, that.currentIndex, '0');
							}
						}
						uni.showToast({
							icon: 'none',
							title: res.data.data.msg
						});
						
					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络错误'
						});
					},
				});
			},
			// 获取收藏列表
			getStoreList() {
				let gData = app.globalData;
				let that = this;
				uni.request({
					url: gData.site_url + 'Topic.GetFavList',
					method: 'GET',
					data: {
						'uid' : that.userinfo.id,
						'token' : that.userinfo.token
					},
					success: res => {
						console.log(res);
						if(res.data.data.code != '0') {
							return;
						}
						let storeList = res.data.data.info;
						let tiList = that.tiList;
						
						for (let idx in storeList) {
							let item = storeList[idx];
							for (let idx2 in tiList) {
								 let idx2_qid = parseInt(idx2) + 1;
								if((item.testid == that.testsid) && (item.qid == idx2_qid)) {
									that.storeData[item.qid] = true;
									that.$set(that.storeData, item.qid, true);
								}
							}
							
						}
						
					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络错误'
						});
					},
				});
			},
			// 收藏/取消题目
			storeQuestion() {
				let gData = app.globalData;
				let that = this;
				
				let type = '';
				if(that.storeData[that.currentQid] !== true) {
					type = 1;
				}else {
					type = 2;
				}
				
				uni.request({
					url: gData.site_url + 'Topic.SetFav',
					method: 'GET',
					data: {
						// uid token testid qid type 1添加 2取消 
						'uid' : that.userinfo.id,
						'token' : that.userinfo.token,
						'testid' : that.testsid,
						'qid' : that.currentQid,
						'type' : type
					},
					success: res => {
						uni.showToast({
							icon: 'none',
							title: res.data.data.msg
						});
						
						if(res.data.data.code == '0') {
							that.$set(that.storeData, that.currentQid, !that.storeData[that.currentQid]);
						}
						
					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络错误'
						});
					},
					complete: () => {}
				});
			},
			// 根据题号切换题目
			checkByTihao(index) {
				this.currentIndex = index;
				this.currentQid = parseInt(index) + 1;
				let type = this.tiList[this.currentIndex].type;
				if(type == '4') {
					 this.getAnswerStr(this.currentIndex);	
				}
				this.showMsk = true;
			},
			// 关闭遮罩上层
			closezhetop() {
				this.is_zhe_top = '0';
				this.is_zhe_show = '0';
				this.strWindow = !this.strWindow; 
			},
			// 弹出遮罩
			openpicker() {
				this.is_zhe_show = '1';
				this.is_zhe_top = '1';
				this.strWindow = !this.strWindow;
			},	 
			// 获取试题题目
			getTopic(testsid, type='0') {
				let gData = app.globalData;
				let that = this;
				uni.request({
					url: gData.site_url + 'Tests.GetTopic',
					method: 'GET',
					data: {
						// uid token testsid type 0新做/重做 1继续做 2只看错误 3看全部
						'uid' : that.userinfo.id,
						'token' : that.userinfo.token,
						'testsid' : testsid,
						'type' : type
					},
					success: res => {
						console.log(res);
						if(res.data.data.code != '0') {
							return;
						}
						that.isShowscore = res.data.data.info[0].showscore;
						that.exp = res.data.data.info[0].exp;
						
						let arr = [];
						if(type == '2') {
							// 只看错误
							let list = res.data.data.info[0].list; 
							for (let idx in list) {
								if(list[idx].rs_user.isok == '1') {
									continue;
								}
								arr.push(list[idx]);
							}
							
						} else {
							arr = res.data.data.info[0].list; 
						}
						that.tiList = arr;
						that.ti_num = that.tiList.length;
						// 错题本赋值
						that.cuotibenData[that.currentIndex] = that.tiList[that.currentIndex].iswrong;
						
						let tiList = that.tiList;
						// 拼接答案字符串(定项和不定项)
						let current_type = that.tiList[that.currentIndex].type;
						if(current_type == '2' || current_type == '5' || current_type == '4') {
							that.getAnswerStr(that.currentIndex, that.tiList[that.currentIndex].type);
						}
						
						// 获取收藏列表
						this.getStoreList();
						
					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络错误'
						});
					},
				});
			},
			// 获取当前索引的答案处理结果
			getAnswerStr(current) {
				let that = this;
				let tiList = that.tiList;
				if(tiList[current].type == '2') {
					let rs_arr = (tiList[current].answer.rs).split(',');
					let rs_str = '';
					for(let current in rs_arr) {
						rs_str += that.ti_info_item_abc[rs_arr[current]] + ', ';
					}
					that.dingxiang.right_ans[current] = rs_str.substring(0, rs_str.lastIndexOf(','));
				} 
				else if(tiList[current].type == '5') { 
					let rs_arr = (tiList[current].answer.rs).split(',');
					let rs_str = '';
					for (let current in rs_arr) {
						rs_str += that.ti_info_item_abc[rs_arr[current]] + ', ';
					}
					that.budingxiang.right_ans[current] = rs_str.substring(0, rs_str.lastIndexOf(','));
				}
				else if(tiList[current].type == '4') {
					// 填空题用户答案打散成数组
					that.tiankong.ans[current] = (tiList[current].rs_user.rs).split(',');
					that.tiankong.right_ans[current] = (tiList[current].answer.ans);
				}
				
			},
			// 数组去重
			uniqueArr(arr) {
				return Array.from(new Set(arr))
		    },
			back() {
				
				uni.navigateBack({
					delta: 1
				});
			},
			handletouchmove: function(event) {
				// console.log(event)
				if (this.flag !== 0) {
					return;
				}
				let currentX = event.changedTouches[0].pageX;
				let tx = currentX - this.lastX;
				let text = '';
				this.mindex = -1;
				//左右方向滑动
				if (tx < 0) {
					text = '向左滑动';
					this.flag = 1;
				   //切换下一题
				   if(this.currentIndex == (this.tiList.length-1)) {
						return;
				   }
				  this.currentIndex++; 
				  this.currentQid++;
				  
				} else if (tx > 0) {
					text = '向右滑动';
					this.flag = 2;
					// 切换上一题
					if(this.currentIndex == '0') {
						return;
					}
					this.currentIndex--; 
					this.currentQid--;
				}
				let type = this.tiList[this.currentIndex].type;
				if(type == '2' || type == '5' || type == '4') {
					 this.getAnswerStr(this.currentIndex);	
				}
				// 错题本赋值
				this.cuotibenData[this.currentIndex] = this.tiList[this.currentIndex].iswrong;
				
				//将当前坐标进行保存以进行下一次计算
				this.lastX = currentX;
				this.text = text;
			},
			handletouchstart: function(event) {
				// console.log(event)
				this.lastX = event.changedTouches[0].pageX;
			},
			handletouchend: function(event) {
				this.flag = 0;
				this.text = '没有滑动';
			},
			
		}
	}
</script>

